<script>
import Vex from 'vexflow'
export default {
  name: 'Home',
  data () {
    return {
      cases: [],
      caseIndex: 0,
      asideWidth: '400px',
      practiceTime: 0,
      showSettings: false,
      settings: {},
      NOTES: ['C', 'D', 'E', 'F', 'G', 'A', 'B'],
      KEYSIGNATURES: [
        {id: 0, label: 'C大调/a小调', value: 'C', width: 60},
        {id: 1, label: 'G大调/e小调', value: 'G', width: 70},
        {id: 2, label: 'D大调/b小调', value: 'D', width: 80},
        {id: 3, label: 'A大调/f#小调', value: 'A', width: 90},
        {id: 4, label: 'E大调/c#小调', value: 'E', width: 100},
        {id: 5, label: 'B大调/g#小调', value: 'B', width: 110},
        {id: 6, label: 'F#大调/d#小调', value: 'F#', width: 120},
        {id: 7, label: 'C#大调/a#小调', value: 'C#', width: 130},
        {id: 8, label: 'F大调/d小调', value: 'F', width: 70},
        {id: 9, label: 'Bb大调/g小调', value: 'Bb', width: 80},
        {id: 10, label: 'Eb大调/c小调', value: 'Eb', width: 90},
        {id: 11, label: 'Ab大调/f小调', value: 'Ab', width: 100},
        {id: 12, label: 'Db大调/bb小调', value: 'Db', width: 110},
        {id: 13, label: 'Gb大调/eb小调', value: 'Gb', width: 120},
        {id: 14, label: 'Cb大调/ab小调', value: 'Cb', width: 130}
      ],
      PRECOLORS: [
        '#FF6666',
        '#FF9900',
        '#FFCC00',
        '#99CC33',
        '#669933',
        '#669999',
        '#006699',
        '#003399',
        '#663366',
        '#990066'
      ]
    }
  },
  created () {
    this.init()
  },
  mounted () {
    console.log('lingling practice 40 hours a day!')
    this.load()
    this.makeIt()
  },
  methods: {
    makeIt () {
      this.practiceTime++
      const _case = this.cases[this.caseIndex]
      this.save()
      // 初始化
      const vf = Vex.Flow
      const div = document.getElementById('pad')
      div.innerHTML = ''
      const renderer = new vf.Renderer(div, vf.Renderer.Backends.SVG)
      // 计算width
      let _firstWidth = this.getFirstWidth(_case)
      let _width = this.settings.staveWidth * 4 + _firstWidth + 50
      // 开始绘制
      let i = 0
      if (_case.treble.switch && _case.bass.switch) {
        // 高低音均有的
        // 高度分层:高音顶部 高音中部 高音底部(低音顶部) 低音中部 低音底部 空隙 (下一条的高音顶部)
        let trebleY = [30, 0, 0]
        let bassY = [0, 0, 0]
        let NOTEHEIGHT = 5
        trebleY[1] = Math.max(_case.treble.range[1] - 41, 0) * NOTEHEIGHT
        trebleY[2] = Math.max(28 - _case.treble.range[0], 0) * NOTEHEIGHT
        bassY[1] = Math.max(_case.bass.range[1] - 27, 0) * NOTEHEIGHT
        bassY[2] = Math.max(14 - _case.bass.range[0], 0) * NOTEHEIGHT
        // 计算height
        let _height = 0
        let lineHeight = trebleY[1] + trebleY[2] + bassY[1] + bassY[2] + 300
        _height = this.settings.count * lineHeight + 20
        renderer.resize(_width, _height)
        const context = renderer.getContext()
        while (i < this.settings.count) {
          const trebleStave = this.makeOneLine(_case.treble, _case.options, _firstWidth, trebleY[0] + trebleY[1], context)
          const bassStave = this.makeOneLine(_case.bass, _case.options, _firstWidth, trebleY[0] + trebleY[1] + trebleY[2] + bassY[1] + 100, context)
          trebleY[0] += lineHeight
          new vf.StaveConnector(trebleStave, bassStave)
            .setType('brace')
            .setContext(context)
            .draw()
          i++
        }
      } else {
        // 只有一条的
        let _clef = null
        let Y = [30, 0, 0]
        let NOTEHEIGHT = 5
        if (_case.treble.switch) {
          _clef = _case.treble
          Y[1] = Math.max(_clef.range[1] - 41, 0) * NOTEHEIGHT
          Y[2] = Math.max(28 - _clef.range[0], 0) * NOTEHEIGHT
        }
        if (_case.bass.switch) {
          _clef = _case.bass
          Y[1] = Math.max(_clef.range[1] - 27, 0) * NOTEHEIGHT
          Y[2] = Math.max(14 - _clef.range[0], 0) * NOTEHEIGHT
        }
        // 计算height
        let _height = 0
        let lineHeight = Y[1] + Y[2] + 150
        _height = this.settings.count * 2 * lineHeight + 50
        renderer.resize(_width, _height)
        const context = renderer.getContext()
        while (i < this.settings.count * 2) {
          this.makeOneLine(_clef, _case.options, _firstWidth, Y[0] + Y[1], context)
          Y[0] += lineHeight
          i++
        }
      }
    },
    makeOneLine (_clef, _options, _FIRSTWIDTH, _y, _context) {
      const vf = Vex.Flow
      const WIDTH = this.settings.staveWidth
      let nowWidth = 20
      const firstStave = new vf.Stave(nowWidth, _y, _FIRSTWIDTH + WIDTH)
      nowWidth += _FIRSTWIDTH + WIDTH
      firstStave.addClef(_clef.name).addTimeSignature('4/4').addKeySignature(this.KEYSIGNATURES[_clef.keySignature].value)
      firstStave.setContext(_context).draw()
      const voice = this.makeVoice(_clef)
      new vf.Formatter().joinVoices([voice]).format([voice], WIDTH - 10)
      voice.draw(_context, firstStave)
      let i = 1
      while (i < 4) {
        const stave = new vf.Stave(nowWidth, _y, WIDTH)
        nowWidth += WIDTH
        stave.setContext(_context).draw()
        const voice = this.makeVoice(_clef)
        new vf.Formatter().joinVoices([voice]).format([voice], WIDTH - 10)
        voice.draw(_context, stave)
        i++
      }
      return firstStave
    },
    makeVoice (_clef) {
      const vf = Vex.Flow
      let i = 0
      const notes = []
      while (i < _clef.duration) {
        i++
        notes.push(this.makeOneNote(_clef))
      }
      const voice = new vf.Voice({num_beats: 4, beat_value: 4})
      voice.addTickables(notes)
      return voice
    },
    makeOneNote (_clef) {
      const vf = Vex.Flow
      let range = _clef.range[1] - _clef.range[0] + 1
      let rootNote = Math.floor((Math.random() * range)) + _clef.range[0]
      const notes = []
      notes.push(this.numToNote(rootNote, this.makeAccidental(_clef)))
      if (_clef.chordSwitch) {
        let nowNote = 0
        let i = 1
        while (i < _clef.chordSize) {
          i += 1
          let newNote = Math.floor((Math.random() * _clef.chordRange)) + 1
          nowNote += newNote
          if (nowNote + rootNote > _clef.range[1] || nowNote > _clef.chordRange) break
          notes.push(this.numToNote(nowNote + rootNote, this.makeAccidental(_clef)).toString())
        }
      }
      const newNote = new vf.StaveNote({clef: _clef.name, keys: notes, duration: _clef.duration.toString(), auto_stem: true})
      if (_clef.accidentalSwitch) {
        const _accidental = this.makeAccidental(_clef)
        if (_accidental !== '') newNote.addAccidental(0, new Vex.Flow.Accidental(_accidental))
      }
      return newNote
    },
    numToNote (val, _accidental) {
      return this.NOTES[val % 7] + _accidental + '/' + Math.floor(val / 7).toString()
    },
    getFirstWidth (_case) {
      return Math.max(this.KEYSIGNATURES[_case.treble.keySignature].width, this.KEYSIGNATURES[_case.bass.keySignature].width)
    },
    makeAccidental (_clef) {
      const tmp = Math.floor((Math.random() * 100))
      if (tmp < _clef.accidentalRange) {
        const tmp2 = Math.floor((Math.random() * 100))
        if (tmp2 > 50) {
          return '#'
        } else {
          return 'b'
        }
      } else {
        return ''
      }
    },
    keySignatureChanged () {
      if (this.cases[this.caseIndex].options.lockKeySignature) {
        this.cases[this.caseIndex].bass.keySignature = this.cases[this.caseIndex].treble.keySignature
      }
    },
    changeCase () {
      this.caseIndex = (this.caseIndex === 2 ? 0 : this.caseIndex + 1)
      this.$message('切换到方案' + (this.caseIndex + 1))
    },
    noteTooltip (val) {
      return this.numToNote(val, '')
    },
    staveWidthTooltip (val) {
      switch (val) {
        case 220:
          return '更窄'
        case 260:
          return '窄'
        case 300:
          return '正常'
        case 340:
          return ' 长'
        case 380:
          return '更长'
        default:
          return '自定'
      }
    },
    chordRangeTooltip (val) {
      return '+' + val + '度'
    },
    accidentalRangeTooltip (val) {
      switch (val) {
        case 0:
          return '无'
        case 25:
          return '较少'
        case 50:
          return '一半'
        case 75:
          return '较多'
        case 100:
          return '全部'
        default:
          return '自定'
      }
    },
    close () {
      this.asideWidth = (this.asideWidth === '400px' ? '0px' : '400px')
    },
    reset () {
      this.asideWidth = '400px'
      this.cases = []
      this.cases.push(this.defaultCase())
      this.cases.push(this.defaultCase())
      this.cases.push(this.defaultCase())
    },
    defaultCase () {
      const _case = {
        treble: '',
        bass: '',
        options: '',
        name: ''
      }
      _case.treble = {
        name: 'treble',
        switch: true,
        chordSwitch: false,
        accidentalSwitch: false,
        keySignature: 0,
        range: [28, 41],
        chordRange: 7,
        chordSize: 3,
        accidentalRange: 50,
        duration: 4
      }
      _case.bass = {
        name: 'bass',
        switch: true,
        chordSwitch: false,
        accidentalSwitch: false,
        keySignature: 0,
        range: [14, 27],
        chordRange: 7,
        chordSize: 3,
        accidentalRange: 50,
        duration: 4
      }
      _case.options = {
        lockKeySignature: true,
        moreNotes: false
      }
      return _case
    },
    defaultSettings () {
      const _settings = {
        autoSave: false,
        showPracticeTime: true,
        staveWidth: 300,
        count: 3,
        color: '#606266'
      }
      return _settings
    },
    init () {
      this.cases = []
      this.cases.push(this.defaultCase())
      this.cases.push(this.defaultCase())
      this.cases.push(this.defaultCase())
      this.settings = this.defaultSettings()
    },
    save () {
      if (this.settings.autoSave) {
        let i = 0
        while (i < 3) {
          this.$cookies.set('case' + i.toString(), this.cases[i], 60 * 60 * 24 * 7)
          i += 1
        }
      }
      this.$cookies.set('settings', this.settings, 60 * 60 * 24 * 7)
    },
    load () {
      if (this.$cookies.get('case1') !== null) {
        let i = 0
        while (i < 3) {
          const _case = this.$cookies.get('case' + i.toString())
          this.cases[i].treble = _case.treble
          this.cases[i].bass = _case.bass
          this.cases[i].options = _case.options
          i += 1
        }
        this.$message('检测到自动保存的配置')
      }
      if (this.$cookies.get('settings') !== null) {
        this.settings = this.$cookies.get('settings')
      }
    },
    remove () {
      if (!this.settings.autoSave) {
        this.$cookies.remove('cases')
      }
    },
    updateLog () {
      setTimeout(() => {
        this.$notify({
          title: 'v1.2 2020-2-25',
          dangerouslyUseHTMLString: true,
          message: '<p>追加\'和弦音符数选择\'</p>',
          duration: 5000
        })
      }, 0)
      setTimeout(() => {
        this.$notify({
          title: 'v1.1 2020-2-12',
          dangerouslyUseHTMLString: true,
          message: '<p>追加\'方案选择与编辑\'</p>' +
            '<p>追加\'主题色选择\'</p>' +
            '<p>美化界面</p>' +
            '<p>原\'显示选项\'调整为\'选项\'和\'设定\'</p>',
          duration: 5000
        })
      }, 50)
      setTimeout(() => {
        this.$notify({
          title: 'v1.0 2020-2-1',
          dangerouslyUseHTMLString: true,
          message: '<p>追加\'调号选择\'</p>' +
            '<p>追加\'低音与高音同调\'</p>' +
            '<p>追加\'更新内容、联系作者\'</p>' +
            '<p>追加\'高低音谱连线\'</p>' +
            '<p>追加\'配置自动保存\'</p>' +
            '<p>追加\'练习次数记录\'</p>' +
            '<p>追加\'扩大音高区间\'</p>' +
            '<p>追加\'显示选项折叠\'</p>' +
            '<p>修复\'音高过高或过低显示错误\'</p>' +
            '<p>修复\'和弦音高有可能超出音高上限\'</p>',
          duration: 5000
        })
      }, 100)
    },
    findZZX () {
      this.$notify({
        title: '联系作者',
        dangerouslyUseHTMLString: true,
        message: '<p>邮箱:zhouzixiao233@gmail.com</p>',
        duration: 5000
      })
    }
  }
}
</script>

<style scoped>
.el-container{
  height: 100%;
}
.options_container{
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  background-color: #FFFFFF33;
}
.cases_container{
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
}
.option_container{
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
}
.option_title_1{
  font-size: large;
  font-weight: bold;
  margin: 5px;
}
.option_title_2{
  margin-top: 10px;
  font-size: small;
  font-weight: bold;
 }
.option_title_3{
}
.el-radio{
  color: white;
}
#pad {
  overflow: auto;
  background-color: #ffffffe8;
}
#flow-refresh-button{
  margin: 0;
  opacity: 0.9;
  font-size: large;
  position: fixed;
  left: 5px;
  top: 10px;
}
#flow-reset-button{
  margin: 0;
  opacity: 0.9;
  font-weight: bold;
  font-size: large;
  position: fixed;
  left: 5px;
  top: 60px;
}
#flow-open-button{
   margin: 0;
   opacity: 0.9;
   font-weight: bold;
   font-size: large;
   position: fixed;
   left: 5px;
   top: 110px;
 }
#flow-changecase-button{
  margin: 0;
  opacity: 0.9;
  font-weight: bolder;
  font-size: large;
  position: fixed;
  left: 5px;
  top: 160px;
}
#flow-time-button{
  margin: 0;
  font-weight: bold;
  font-size: large;
  position: fixed;
  right: 60px;
  top: 10px;
}
#flow-settings-button{
  margin: 0;
  font-weight: bold;
  font-size: large;
  position: fixed;
  right: 5px;
  top: 10px;
}
</style>



